  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

.register-form {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.register-form h2 {
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
  color: #333;
}

.register-form input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.register-form button {
  width: 100%;
  padding: 10px;
  background-color: #00bfa6;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 20px;
}

.register-form button:hover {
  background-color: #019f87;
}

.login-link {
  margin-top: 20px;
}

.login-link a {
  color: #007bff;
  text-decoration: none;
}

.login-link a:hover {
  text-decoration: underline;
}

.custom-navbar {
  background-color: rgb(2, 115, 159);
  padding: 1rem 2rem;
}

.custom-navbar .navbar-nav {
  margin-left: 0;
}

.custom-navbar .navbar-brand {
  margin-left: 0;
  font-size: 32px;
}

.custom-navbar .navbar-toggler {
  border: none;
}

.nav-link {
  padding: 0.75rem 1.5rem;
  color: white;
  font-size: 24px;
}

.nav-link.active {
  font-weight: bold;
  color: rgb(49, 155, 91);
}

.custom-footer {
  background-color: rgb(2, 115, 159);
  color: white;
  text-align: center;
  padding: 1rem 0;
  font-size: 18px;
}

.error {
  color: red;
  font-size: 14px;
  margin-top: -10px;
  margin-bottom: 10px;
}

